// 
const loginSuccess = {
    state: 0, // 0
    //group: Mock.Random.integer(0, 1), // 01
    token: "123456", // 
}
const DraftSuccess = {
    state:0,//
}

const SIGNSuccess = {
    state : 0,//
    constructs : ["Construct 1", "Construct 2", "Construct 3"],
}

// 
const loginFail1 = {
    state: 1, // 1
}

// 
const loginFail2 = {
    state: -1, // -1
}

// 
const RSuccess = {
    state: 0, // 0
    //group: Mock.Random.integer(0, 1), // 01
    token: '123456', // 
}

// 
const RFail = {
    state: 1, // 1
}

// 
//  http://localhost:10087/user/login
Mock.mock('http://localhost:10087/user/login', 'post', (options) => {
    // 
    const params = new URLSearchParams(options.body)
    const user = params.get('user')
    const passwd = params.get('passwd')
    // 
    if (user === 'admin' && passwd === '123456') {
        // 1
        return {
            ...loginSuccess,
            group: 2,
            // 
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'POST',
                'Access-Control-Allow-Headers': 'Content-Type'
            }
        }
    } else if (user === 'user' && passwd === '654321') {
        // 0
        return {
            ...loginSuccess,
            group: 1,
            // 
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'POST',
                'Access-Control-Allow-Headers': 'Content-Type'
            }
        }
    } else if (user !== 'admin' && user !== 'user') {
        // 
        return {
            ...loginFail1,
            // 
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'POST',
                'Access-Control-Allow-Headers': 'Content-Type'
            }
        }
    } else {
        // 
        return {
            ...loginFail2,
            // 
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'POST',
                'Access-Control-Allow-Headers': 'Content-Type'
            }
        }
    }
})


Mock2.mock('http://localhost:10087/user/register', 'post', (options) => {
    // 
    //alert("")
    const params = new URLSearchParams(options.body)
    const email = params.get('email')
    const user = params.get('user')
    const passwd = params.get('passwd')
    // 
    if (user === '11' && passwd != '' && email!='') {
        // 1
        return {
            ...RSuccess,
            group: 1,
            // 
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'POST',
                'Access-Control-Allow-Headers': 'Content-Type'
            }
        }
    } else {
        // 
        return {
            ...RFail,
            // 
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'POST',
                'Access-Control-Allow-Headers': 'Content-Type'
            }
        }
    }
})

