// mock.js
import Mock from 'mockjs'


// 
Mock.mock("http://localhost:10087/contract/watch", 'post', (options) => {
    // !!!!!!!!!!
    //=[object Object] [undefined] 123
    const params = new URLSearchParams(options.body)
    const contract_name = params.get('contract_name')
    const user_name = params.get('user_name')
    const content = params.get('content')
    alert("="+contract_name+" ["+user_name+"] "+content)
    const token = params.get('token')
    // token
    if (token === '123456') {
        //alert("hhhhhhhhhh")
        // 
        return {
            code: 200,
            message: 'success',
        }
    } else {
        // 
        return {
            code: token,
            message: "invalid"
        }
    }
})
