// mock.js
import Mock from 'mockjs'

// 
const contracts = Mock.mock({
    'contracts|5': [{
        'id|+1': 1,
        'name': '@cname',
        'date': "2023.6.13"
    }]
})

// 
Mock.mock("http://localhost:10087/manager/display/search", 'post', (options) => {
    //  user)
    alert("/contract/selectContractByType")
    
    const params = new URLSearchParams(options.body)
    const matter = params.get('matter')
    const token = params.get('token')
    const sign = params.get("type")
    //alert("hhhhhhhhhh")
    // 
    if(matter === '1'||matter === '2'){
       
        alert("/")
    }
    //     alert("")
    return {
        code: token,
        message: 'success',
        contracts: contracts.contracts.map((item, index) => {
            // customer
            //alert("sendtocontract:"+ item.name)
            if(sign === "sign"){
                //alert("sign ctrct")
                return {
                    id: item.id,
                    name: item.name,
                    beginTime:item.date,
                    index: index
                }
            }
            else {
                return {
                    id: item.id,
                    name: item.name,
                    date:item.date,
                    index: index
                }
            }
        })
    }
})
